@import url('https://fonts.googleapis.com/css2?family=Lora&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500&display=swap');

body, html {
  overflow: hidden;
}


:root {
  --base-color1: #fff;
  --base-color2: rgb(31, 30, 30);
  --base-color3: #e8e8e8;
  --color: rgb(139, 83, 228);
  --color2: #9fddf9;
  --color3: #f27fb2;
  --base-font-title: 'Poppins', sans-serif;
  --base-font-body: 'Lora', serif;
  --font-size-large: 20px;
  --font-size-medium: 20px;
  --font-size-small: 12px;
  --window-width: calc(100% - 120px);
  --window-margin: 120px;
  --window-margin-top: 18vh;
  --top-margin-menu: 0;
  --content-margin: 10vw;
}

::-moz-selection {
  background: var(--color);
  color: var(--color2);
  mix-blend-mode: color-dodge;
}
::selection {
  background: var(--color2);
  color: var(--color);
  mix-blend-mode: color-dodge;
}

body {
  margin: 0;
  /* background: #fff; */
  color: #000;
  transition: all 0.2s ease;
  overflow-x:hidden;
  /* background-color: #f3f3f3;
  background-image: url("https://www.transparenttextures.com/patterns/bedge-grunge.png"); */
  /* overflow: hidden; */
}

ul {
  list-style-type:none;
  padding:0;
  margin:0;
}

.dark-mode {
  background: var(--base-color2);
  color: var(--base-color3);
}


#cursor {
  position: absolute;
  width: 30px;
  height: 30px;
  top: -50%;
  left: -50%;
  margin: -15px 0 0 -15px;
  border-radius: 50%;
  /* border: 4px solid #fff; */
  background-color: var(--color);
  backface-visibility: hidden;
  -webkit-transition: all 0.2s ease-out;
  -moz-transition: all 0.2s ease-out;
  -o-transition: all 0.2s ease-out;
  -ms-transition: all 0.2s ease-out;
  transition: all 0.2s ease-out;
  mix-blend-mode: color-dodge;
  z-index: 1000;
  pointer-events: none;
}
#cursor.is-moving {
  -webkit-transform: scale(2);
  -moz-transform: scale(2);
  -o-transform: scale(2);
  -ms-transform: scale(2);
  transform: scale(2);
}

#cursor.highlight {
  -webkit-transform: scale(3);
  -moz-transform: scale(3);
  -o-transform: scale(3);
  -ms-transform: scale(3);
  transform: scale(3);
  border: 1px solid var(--color);
  /* background: transparent; */
  background: rgba(139, 83, 228, 0.1);
}

#cursor.normal {
  mix-blend-mode: normal;
  opacity: 0.5;
}

#cursor.hidden {
  display: none;
}


.buttonEl {
  cursor: pointer;
  z-index: 50;
}

#cursor.highlight:after {
  content: "";
  left: 50%;
  top: 50%;
  position: absolute;
  width: 5px;
  height: 5px;
  border-top: 1px solid var(--color);
  border-right: 1px solid var(--color);
  -webkit-transform: translate(-50%, -50%);
  -moz-transform: translate(-50%, -50%);
  -o-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}

#cursor.highlight:before {
  content: "";
  left: 50%;
  top: 50%;
  position: absolute;
  width: 12px;
  height: 0;
  border-top: 1px solid var(--color);
  -webkit-transform: translate(-61.5%, 1px) rotate(-45deg);
  -moz-transform: translate(-61.5%, 1px) rotate(-45deg);
  -o-transform: translate(-61.5%, 1px) rotate(-45deg);
  -ms-transform: translate(-61.5%, 1px) rotate(-45deg);
  transform: translate(-61.5%, 1px) rotate(-45deg);
}


