#image-cursor {
  position: fixed;
  display: inline-block;
  width: 20vw;
  height: 20vw;
  top: 40%;
  left: 40%;
  margin: 0;
  background-size: contain;
  background-repeat: no-repeat;
  mix-blend-mode: multiply;
  opacity: 0;
  -webkit-transition: all 0.5s ease-out;
  -moz-transition: all 0.5s ease-out;
  -o-transition: all 0.5s ease-out;
  -ms-transition: all 0.5s ease-out;
  transition: all 0.5s ease-out;
  z-index: 1;
  pointer-events: none;
}

#image-cursor.darkMode {
  mix-blend-mode: screen;
}

#message-box {
  position: fixed;
  display: none;
  /* opacity: 0; */
  top: 30%;
  left: 50%;
  margin-left: 60px;
  -webkit-transform: translate(-50%, -50%);
  -moz-transform: translate(-50%, -50%);
  -o-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  width: 350px;
  height: auto;
  padding: 20px 30px;
  box-sizing: border-box;
  background: rgb(79, 79, 79);
  border: 1px solid #000;
  color: #ccc;
  font-size: 18px;
  font-family: var(--base-font-title);
  z-index: 1000;
  pointer-events: none;
}

#message-box.visible {
  /* opacity: 1; */
  display: inline-block;
  /* z-index: 1000; */
}

.div-container-element {
  display: inline;
  position: relative;
}

.page-wrapper {
  height: 100vh;
  overflow-y: auto;
  overflow-x: hidden;
}
.page-wrapper.haptic {
  scroll-snap-type: y proximity;
}

.page-section {
  left:0;
  top:0;
  width: var(--window-width);
  height: 100vh;
  min-height: 650px;
  margin-left: var(--window-margin);
  position: relative;
  display: inline-block;
  /* background: #fff; */
}

.page-section.darkMode {
  background: var(--base-color2);
}

#svgBg {
  position:absolute;
  width: 100%;
  height: 100vh;
  left:0;
  top:0;
}


.section-title {
  font-family: var(--base-font-title);
  font-size: 4em;
  font-weight: 500;
  margin-bottom: 10vh;
  animation: fadeInAnimation ease 1.5s;
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
}

.body-title {
  font-size: 32px;
  margin: 20px 0 10px;
}

.body-text, .publication-book {
  display: inline;
  font-family: var(--base-font-title);
  font-size: 18px;
  line-height: 30px;
  /* font-size: 1.08vw;
  line-height: 1.85vw; */
  font-weight: 300;
}

.publication-type {
  display: inline-block;
  background: var(--color);
  font-family: var(--base-font-title);
  padding: 2px 10px;
  font-size: 14px;
  line-height: 20px;
  height: 20px;
  border-radius: 20px;
  text-align: center;
  color: #eee;
  text-transform: uppercase;
  font-weight: 500;
  user-select: none;
}

.vertical-aligned-textblock {
  display: inline-block;
  position: relative;
  top: 50%;
  transform: translateY(-60%);
  margin: 0 45vw 0 var(--content-margin);
  animation: fadeInAnimation ease 1.5s;
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
}


@keyframes fadeInAnimation {
  0% {
      opacity: 0;
      margin-top: 60px;
  }
  100% {
      opacity: 1;
      margin-top: 0;
   }
}


h1  {
  font-family: var(--base-font-title);
  font-weight: 500;
  font-size: 3vw;
  margin-top: 0;
}

.basic-text-style-large {
  font-family: var(--base-font-body);
  font-size: 22px;
  line-height: 36px;
}




.social-media-list {
  display: inline-flex;
  flex-direction: row;
}

.social-media-list li {
  margin: 50px 50px 50px 0px;
}

.social-media-icon {
  width: 70px;
  height: 70px;
  padding: 5px;
  box-sizing: border-box;
  cursor: pointer;
}

.social-media-icon:active {
  padding: 8px 2px 2px 8px;
}


.social-media-icon.hovereffect:hover .social-media-icon-svg {
  stroke: #fff;
  fill: transparent;
}

.social-media-icon.hovereffect:active .social-media-icon-svg {
  fill: #fff;
  stroke: transparent;
}


.arrow-link {
  position: relative;
  display:inline-block;
  top: -6px;
  left: 0;
  margin-left: 10px;
  width: 30px;
  height: 0;
  border-top: 2px solid #fff;
  opacity: 0.5;
  padding: 0 10px;
  -webkit-tap-highlight-color: transparent;
}

.arrow-link.hovereffect {
  -webkit-transition: all 0.2s ease-in-out;
  -moz-transition: all 0.2s ease-in-out;
  -o-transition: all 0.2s ease-in-out;
  -ms-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;
}

.arrow-link.explanation {
  border-top: 3px solid var(--color);
}

.arrow-link:after {
  content: '';
  position: absolute;
  width: 10px;
  height: 10px;
  -webkit-transform: translate(0, -58%) rotate(45deg);
  -moz-transform: translate(0, -58%) rotate(45deg);
  -o-transform: translate(0, -58%) rotate(45deg);
  -ms-transform: translate(0, -58%) rotate(45deg);
  transform: translate(0, -58%) rotate(45deg);
  border-right: 2px solid #fff;
  border-top: 2px solid #fff;
  right: 0;
}

.arrow-link.explanation::after {
  border-right: 2px solid  var(--color);
  border-top: 2px solid  var(--color);
}

.site-explanation {
  position: relative;
  display:inline-block;
  font-family: var(--base-font-body);
  font-size: 21px;
  line-height: 33px;
  color: var(--color);
  margin-top: 50px;
}


.site-explanation:hover .arrow-link {
  margin-left: 10px;
  opacity: 0.5;
}
.site-explanation:hover .arrow-link.hovereffect {
  margin-left: 20px;
  opacity: 1;
}


#sidebar-menu.open .outline, #sidebar-menu.dark-mode .outline, .mobile-multimodal-menu .outline, .mobile-multimodal-menu-front-page.darkMode .outline {
  fill: transparent;
  stroke: #fff!important;
}

#sidebar-menu.open .fill, #sidebar-menu.dark-mode .fill, .mobile-multimodal-menu .fill, .mobile-multimodal-menu-front-page.darkMode .fill  {
  fill: #fff;
  stroke: transparent;
}

#scroll-msg.darkMode .scrollImage {
  fill: #fff;
}

@media screen and (max-width: 1600px) {

  .basic-text-style-large {
    font-size: 20px;
    line-height: 32px;
  }

  .site-explanation {
    font-size: 19px;
    line-height: 32px;
  }

}

@media screen and (max-width: 1400px) {
  :root {
    --font-size-large: 20px;
    --font-size-medium: 20px;
    --font-size-small: 12px;
    --window-width: 100%;
    --window-margin: 0;
    --content-margin: 8vw;
    --top-margin-menu: 110px;
  }

  h1  {
    font-size: 4vw;
  }

  .section-title {
    font-size: 5vw;
    margin-bottom: 6vh;
  }


  .site-explanation {
    margin-top: 20px;
  }

  .page-section {
    margin-top: 110px;
  }

  .vertical-aligned-textblock {
    -webkit-transform: none;
    -moz-transform: none;
    -o-transform: none;
    -ms-transform: none;
    transform: none;
    top:0;
    margin: 0 var(--content-margin);
  }

  ul.social-media-list.contact {
    flex-direction: column!important;
  }

  ul.social-media-list.contact li {
    margin: 20px 0!important;
  }

  #svgBg {
    position:relative;
    margin-top: 60px;
    width: 100%;
    height: 70vw;
  }

  .page-wrapper.haptic {
    scroll-snap-type: y proximity;
    scroll-padding: var(--top-margin-menu);
  }

  .page-section.start {
    margin-top: var(--top-margin-menu);
    padding-top: 80px;
    height: 60vw;
  }

}

@media screen and (max-width: 1300px) {
  :root {
    --font-size-large: 20px;
    --font-size-medium: 20px;
    --font-size-small: 12px;
    --window-width: 100%;
    --window-margin: 0;
  }

  h1  {
    font-size: 4.5vw;
  }

  .section-title {
    font-size: 5vw;
    margin-bottom: 6vh;
  }

  .body-title {
    font-size: 28px;
  }

  .body-text, .publication-book {
    font-size: 18px;
    line-height: 30px;
  }


}


@media screen and (max-width: 1000px) {
  :root {
    --font-size-large: 20px;
    --font-size-medium: 20px;
    --font-size-small: 12px;
    --window-width: 100%;
    --window-margin: 0;
    --content-margin: 9vw;
    --window-margin-top: 80px;
  }

  #cursor, #image-cursor {
    display: none;
  }

  h1  {
    font-size: 35px;
    margin-bottom: 60px;
  }

  .basic-text-style-large {
    font-size: 18px;
    line-height: 30px;
  }

  .vertical-aligned-textblock {
    top:0;
    margin: 0 var(--content-margin);
    padding: 50px 0;
  }

  .section-title {
    font-size: 5vw;
    margin-bottom: 6vh;
  }

  #svgBg {
    margin-top: 0;
    position:relative;
  }

  .page-section, .page-section.start {
    height: auto;
    min-height: 0;
    padding: 30px 0;
  }



  .page-section.selected-work {
    margin-top:0;
    padding-bottom: 3vh;
  }

  .body-title {
    font-size: 22px;
    line-height: 32px;
    margin: 20px 0 10px;
  }

  .page-section.start {
    padding-top: 60px;
  }


  .body-text, .publication-book {
    font-size: 15px;
    line-height: 26px;
  }

  .site-explanation {
    font-size: 17px;
    line-height: 27px;
    margin-top: 20px;
  }




  .social-media-list.contact li {
    margin: 50px 0;
  }

  .arrow-link {
    top: -4px!important;
    width: 15px!important;
  }


  .arrow-link:after {
    width: 6px!important;
    height: 6px!important;
    /* top: -6.5px; */
  }

}


@media screen and (max-width: 800px) {


  .basic-text-style-large {
    font-size: 16px;
    line-height: 28px;
  }

  .section-title {
    font-size: 28px;
  }

  #svgBg {
    margin-top: 50px;
    position:relative;
  }
}


@media screen and (max-width: 700px) {
  :root {
    --top-margin-menu: 76px;
  }

}
